import React from 'react'
import { Navbar,Nav } from 'react-bootstrap';
import {Link } from 'react-router-dom';
import logo from './download.png';

export default function NavBar() {
    return (
        <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Navbar.Brand href="/covid">
            <img src={logo} alt={logo} style={{width:30, height:30,margin:3}}/>
            Covid-19 Tracker
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="ml-auto">
                <Nav.Item >
                    <Nav.Link>
                        <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to="/covid">Global</Link>
                    </Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link>
                    <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to="/India">India</Link>
                    </Nav.Link>
                </Nav.Item>
            </Nav>
        </Navbar.Collapse>
        </Navbar>
    )
}
