import React,{useEffect, useState} from 'react'
import {fetchstate,statedata,fetchdaily} from './api/index'
import {FormControl,NativeSelect} from '@material-ui/core'
import { Bar, Line } from 'react-chartjs-2';
import numeral from 'numeral';
import logo from './logo.jpg';

const India=()=> {

    const [fetchedState,setFetchedState]=useState([]);
    const [data,setData]=useState({}); 
    const [dailyData,setDailyData]=useState([]);

    useEffect(()=>{
        const fetchstates=async()=>{
            setFetchedState(await fetchstate());
            setData(await statedata());
            setDailyData(await fetchdaily());
        }
        fetchstates();

    },[setFetchedState]);

    const handleChange=async(state)=>{
        setData(await statedata(state));
    }

    const barChart = (
        data.Confirmed ? (
          <Bar
            data={{
              labels: [ 'Active', 'Recovered', 'Deaths'],
              datasets: [
                {
                  label: 'People',
                  backgroundColor: ['rgba(0, 0, 255, 0.6)','rgba(0, 255, 0, 0.6)', 'rgba(255, 0, 0, 0.6)'],
                  hoverBackgroundColor: ['rgba(0, 0, 255, 0.9)',  'rgba(0, 255, 0, 0.9)', 'rgba(255, 0, 0, 0.9)'],
                  data: [ data.Active, data.Recovered, data.Death],
                },
              ],
            }}
            options={{
              legend: { display: false },
              scales: {
                yAxes: [{
                  gridLines:{
                    display:false
                  },
                  ticks: {
                    callback:function(value,index,values){
                        return numeral(value).format("0a")
                    }
                  },
                }],
              },
            }}
          />
        ) : null
      );

      const lineChart = (
        dailyData[0] ? (
          <Line
            data={{
              labels: dailyData.map(({ date }) => date),
              datasets: [{
                data: dailyData.map((data) => data.dailyconfirmed),
                label: 'Infected',
                borderColor: 'blue',
                
              }, 
              {
                data: dailyData.map((data) => data.dailyrecovered),
                label: 'Recovered',
                borderColor: 'green',
                
              },
              {
                data: dailyData.map((data) => data.dailydeceased),
                label: 'Deaths',
                borderColor: 'red',
              },
              ],
            }}
            options={{
              elements:{
                point:{
                  radius:1
                }
              },
              scales: {
                xAxes:[{
                  ticks:{
                      callback: function(value) { 
                      return new Date(value).toLocaleDateString('en-EN', {day: 'numeric',month:'short'}); 
                  },
                  }
                }],
                yAxes: [{
                  gridLines:{
                    display:false
                  },
                  ticks: {
                    callback:function(value,index,values){
                        return numeral(value).format("0a")
                    }
                  },
                }]
              },
            }}
          />
        ) : null
      );
    
    return (
        <div className="container">
          <img className="image" src={logo} alt={logo}/>
            <div>
             <FormControl className="formControl" >
                <NativeSelect  onChange={(e)=>handleChange(e.target.value)} >
                    {fetchedState.map((state,i)=><option style={{color:"#FFFAF0",background:"#151515"}} key={i} value={state}>{state}</option>)}
                </NativeSelect>
            </FormControl>
            </div>
            <h6>Last Updated: {data.LastUpdate}</h6>
            <div className="barchart">
                {barChart}
            </div>
            <br />
            <br />
            <h5>Country Daily Data</h5>
            <div className="linechart">
              {lineChart}
            </div>
        </div>
    )
}

export default India;