import axios from 'axios';

const url ='https://api.covid19api.com/summary';
const url2='https://api.covid19india.org/data.json';

export const fetchdata =async (country) =>{
    try {
        if(!country)
        {
            const {data:{Global}}= await axios.get(url);

            const mData={
                
                NewConfirmed: Global.NewConfirmed,
                NewDeaths: Global.NewDeaths,
                NewRecovered: Global.NewRecovered,

                TotalConfirmed: Global.TotalConfirmed,
                TotalDeaths: Global.TotalDeaths,
                TotalRecovered: Global.TotalRecovered,
            }
            return mData;
    }
    else{
        const {data:{Countries}}= await axios.get(url);
        const selectedCountry=Countries.filter((e)=>e.Country===country)[0];

        const mData={
            
            NewConfirmed: selectedCountry.NewConfirmed,
            NewDeaths: selectedCountry.NewDeaths,
            NewRecovered: selectedCountry.NewRecovered,

            TotalConfirmed: selectedCountry.TotalConfirmed,
            TotalDeaths: selectedCountry.TotalDeaths,
            TotalRecovered: selectedCountry.TotalRecovered,
        }

        return mData;
    }
    } catch (error) {
        console.log(error)
    }
} 

export const fetchcountry=async()=>{
    try {
        const {data:{Countries}}=await axios.get(url);
        return Countries.map((country)=>country.Country);
    } catch (error) {
        console.log(error)
    }
}

export const fetchstate=async()=>{
    try {
        const {data:{statewise}}=await axios.get(url2);
        return statewise.map((state)=>state.state)
    } catch (error) {
        console.log(error)
    }
}

export const statedata=async(state)=>{
    try {
        const {data:{statewise}}=await axios.get(url2);
        var selectedState='';
        if(!state)
        {
            selectedState=statewise.filter((e)=>e.state==="Total")[0];
        }
        else{
            selectedState=statewise.filter((e)=>e.state===state)[0];
            
        }
        const mData={
            State:selectedState.state,
            Confirmed:selectedState.confirmed,
            Active:selectedState.active,
            Recovered:selectedState.recovered,
            Death:selectedState.deaths,
            LastUpdate:selectedState.lastupdatedtime,
            
        }
        return mData;
       
    } catch (error) {
        console.log(error);
    }
}

export const fetchdaily=async()=>{
    try {
        const {data:{cases_time_series}}=await axios.get(url2);
        return cases_time_series;
    } catch (error) {
        console.log(error);
    }
}
